/* In Header.css */

/* Styles for the header element */
.header {
  background: linear-gradient(135deg, #a1c4fd 10%, #c2e9fb 100%);
  color: rgb(9, 7, 7);
  display: flex;
  padding: 100px;
  text-align: left;
  font-size: 20px;
}

/* Styles for the content inside the header */
.header-content {
  display: flex;
  align-items:flex-start ;
  justify-content: center;
}

/* Styles for the image inside the header */
.header-image {
  max-width: 35%;
  border-radius: 10px;
  border-style: solid;
  border-width: 2px;
  border-image: linear-gradient(90deg, #bdc2e8, #e6dee9) 1;
  margin-left: 0%;
}

/* Styles for the name/text inside the header */
.header-name {
  font-size: 50px; /* Adjust the font size as needed */
  margin-left: 30px;
}